
import {ref} from 'vue';
import ProjectDetailsOverlay from "@/components/ProjectDetailsOverlay.vue";
import ProjectData from "@/data/ProjectData";

export default {
  name: "ProjectsList",
  components: {
    ProjectDetailsOverlay,
  },
  props: {
    projects: Array as () => ProjectData[],
  },
  setup() {
      const showPopup = ref(false);
      const popupTitle = ref("");
      const popupColor = ref("");
      const popupContent = ref("");

      const showDetails = (item: ProjectData) => {
        popupTitle.value = item.name;
        popupColor.value = item.accentColor;
        popupContent.value = item.htmlDescription;
        showPopup.value = true;
        window.scrollTo(0, 0);
      };

      return { showPopup, popupTitle, popupColor, popupContent, showDetails };
  }
};
